import {
  getModule,
  Module,
  MutationAction,
  Mutation,
  Action,
  VuexModule,
} from 'vuex-module-decorators';
import store from '@/store';
import menuservice from '@/store/services/menuDataService';
import { Menu } from '../interfaces/Menu';

@Module({
  namespaced: true,
  name: 'menu',
  store,
  dynamic: true,
})



class menuModule extends VuexModule {

  menus: Menu[] = [];

  @MutationAction({ mutate: ['menus'] })
  async getAll() {
    const menus = await menuservice.getAll();

    return { menus };

  }

  @MutationAction({ mutate: ['menus'] })
  async createMenu(menu: Menu) {
    const menus = await menuservice.create(menu);

    if (menus.data.code = 201) {
      const menus = await menuservice.getAll();
      return { menus };

    }
    return { menus };

  }



}
export default getModule(menuModule);
