








































import { Vue, Component, Prop, Ref } from "vue-property-decorator";
import menuModule from "@/store/modules/menuModule";
import { Menu } from "@/store/interfaces/Menu";
import Form from "./Form.vue";
@Component({
  components: {
    Form
  }
})
export default class List extends Vue {
  //
  activarform: boolean = false;
  menu: Menu[] | Menu = [];
  //fields:Array<string> = ['id', 'name', 'title', 'order', 'icon', 'route', 'children' ]
  fields: Array<any> = [
    { key: "id", _style: "width:5%" },
    { key: "name", _style: "width:20%" },
    { key: "title", _style: "width:10%" },
    { key: "order", _style: "width:10%" },
    { key: "icon", _style: "width:10%" },
    { key: "route", _style: "width:10%;" },
    { key: "children", _style: "width:5%;" },
    {
      key: "show_details",
      label: "Acciones",
      _style: "width:1%",
      sorter: false,
      filter: false
    }
  ];

  modificar: boolean = false;
  async created() {
    menuModule.getAll();
  }

  get menus() {
    return menuModule.menus;
  }

  modificarMenu(item: any) {
    this.menu = item;
    this.activarmodal(true);
  }

  activarmodal(valor: boolean) {
    this.activarform = valor;
  }

}
